import './loading.css'
import zairuImg from '../assets/loadIcon.gif'
export const Loading = (e) => {
  // 
  var htm = ''
  var htmChild = ''
  var mb
  var w_width = window.innerWidth
  var w_height = window.innerHeight
  // 
  var type = e.type
  var text = e.text
  // 
  if (e) {
    if (!text) text = 'LOADING'
  }
  htm = document.createElement('div')
  // 
  if (type) {
    htm.className = 'popups'
    htm.id = 'popups'
    htmChild += "<div id='mengban' class='mengban'></div>"
    htmChild += "<div class='loaidngWrap'>"

    // 
    htmChild += "<img class='LoadingImg' src='" + zairuImg + "' alt='icon'/>"
    htmChild += "<p class='loadingContent'>" + text + '</p>'

    htmChild += '</div>'
    htm.innerHTML = htmChild

    // body
    console.log('document.body.append',document.body)
    document.body.appendChild(htm)
    mb = document.getElementById('popups')
    // 
    // var mengban = document.getElementById('mengban')
    // if (mengban) {
    //   mengban.onclick = function () {
    //     if (mb) {
    //       mb.remove()
    //     }
    //   }
    // }
  } else {
    // 
    mb = document.getElementById('popups')
    if (mb) {
      mb.remove()
    }
  }
}

export default { Loading }
